@import url('https://fonts.googleapis.com/css?family=Montserrat:600|Raleway:500,600,700');

.app {
  background-image: -webkit-gradient(linear,
    left top, right top,
    from(rgba(255, 255, 255, 0.75)) ,
    to(rgba(255, 255, 255, 0.4))),
  url('contact.png');
  background-image: -webkit-linear-gradient(left,
    rgba(255, 255, 255, 0.75) ,
    rgba(255, 255, 255, 0.4)),
  url('contact.png');
  background-image: -o-linear-gradient(left,
    rgba(255, 255, 255, 0.75) ,
    rgba(255, 255, 255, 0.4)),
  url('contact.png');
  background-image: linear-gradient(to right,
    rgba(255, 255, 255, 0.75) ,
    rgba(255, 255, 255, 0.4)),
  url('contact.png');
  position: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-clip: cover;
  background-size: cover;
  background-attachment: fixed;
  overflow-x: hidden; 
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: start;
      justify-content: flex-start;
  /* flex-direction: row; */
}

.scrollable {
  background: rgba(255, 255, 255, 0.9);
  height: -moz-fit-content;
  height: -webkit-fit-content;
  height: fit-content;
  position: absolute;
  top: 91vh;
  width: 650px;
  margin-left: 50%;
  margin-right: 50px;
  text-align: center;

  -webkit-transition: all .5s ease-in;
  -o-transition: all .5s ease-in;
  transition: all .5s ease-in;
}

.scrollable:hover {
  background: #ffffff;

  -webkit-transition: all .5s ease-in;
  -o-transition: all .5s ease-in;
  transition: all .5s ease-in;
}

.scrollable .chevron {
  margin-top: 20px;
  position: relative;
  -webkit-animation: MoveUpDown 1.5s linear infinite;
          animation: MoveUpDown 1.5s linear infinite;
}


@-webkit-keyframes MoveUpDown {
  0%, 100% {
    top: 5px;
  }
  50% {
    top: 15px;
  }
}


@keyframes MoveUpDown {
  0%, 100% {
    top: 5px;
  }
  50% {
    top: 15px;
  }
}

.scrollable h1 {
  font-family: 'Montserrat', sans-serif;
  font-size: 2rem;
  font-weight: 600;
  color: #35a6de;
}

p {
  font-family: 'Raleway', sans-serif;
  font-size: 1rem;
  font-weight: 500;
  color: #333333;
  text-align: left;
}

.scrollable .profile {
  display: -ms-flexbox;
  display: flex;
  margin-left: 25px;
  margin-right: 25px;
  -ms-flex-align: center;
      align-items: center;
}

.scrollable .profile img {
  width: 110px;
  height: 110px;
  margin-left: 15px;
  border-radius: 50%;  
}

.scrollable .profile .summary {
  font-family: 'Raleway', sans-serif;
  font-size: 1rem;
  font-weight: 500;
  color: #333333;
  text-align: left;
  margin-left: 30px;
}


.scrollable h2 {
  font-family: 'Raleway', sans-serif;
  font-size: 1rem;
  font-weight: 500;
  color: #333333;
  text-align: left;
  font-style: normal;
  margin-left: 25px;
  margin-bottom: 20px;
}

.scrollable em {
  font-family: 'Raleway', sans-serif;
  font-size: 1rem;
  font-weight: 600;
  color: #35a6de;
  text-align: left;
  font-style: normal;
}

.scrollable .skills {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin-top: 10px;
  margin-left: 25px;
  margin-right: 25px;
}

.scrollable .skills em::before {
  content: "â€¢ ";
}

.scrollable .skills .left {
  margin-left: 15px;
  -ms-flex: 1 1;
      flex: 1 1;
}

.scrollable .skills .list {
  -ms-flex: 2 1;
      flex: 2 1;
}

.scrollable .skills .list .languages em {
  font-weight: 800;
}

.scrollable .skills .list .frameworks em {
  font-weight: 600;
}

.scrollable .skills .list .software em {
  font-weight: 400;
}

@-webkit-keyframes strikein {
  0%     { -webkit-transform-origin:  0% 50%; transform-origin:  0% 50%;-webkit-transform:scaleX(0);transform:scaleX(0); }
  50%    { -webkit-transform-origin:  0% 50%; transform-origin:  0% 50%;-webkit-transform:scaleX(1);transform:scaleX(1); }
}

@keyframes strikein {
  0%     { -webkit-transform-origin:  0% 50%; transform-origin:  0% 50%;-webkit-transform:scaleX(0);transform:scaleX(0); }
  50%    { -webkit-transform-origin:  0% 50%; transform-origin:  0% 50%;-webkit-transform:scaleX(1);transform:scaleX(1); }
}
@-webkit-keyframes strikeout {
  50%    { -webkit-transform-origin:  0% 50%; transform-origin:  0% 50%;-webkit-transform:scaleX(1);transform:scaleX(1); }
  0%     { -webkit-transform-origin:  0% 50%; transform-origin:  0% 50%;-webkit-transform:scaleX(0);transform:scaleX(0); }
}
@keyframes strikeout {
  50%    { -webkit-transform-origin:  0% 50%; transform-origin:  0% 50%;-webkit-transform:scaleX(1);transform:scaleX(1); }
  0%     { -webkit-transform-origin:  0% 50%; transform-origin:  0% 50%;-webkit-transform:scaleX(0);transform:scaleX(0); }
}

.strike {
  position: relative;
  -webkit-animation: strikein .75s ease-in-out forwards;
          animation: strikein .75s ease-in-out forwards;
}
.strike:hover:after {
  content: ' ';
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  height: 3px;
  background: #35a6de;
  -webkit-animation: strikein .75s ease-in-out forwards;
          animation: strikein .75s ease-in-out forwards;
}

@media screen and (max-width: 600px) {

  .app {
    -ms-flex-pack: center;
        justify-content: center;
    background-image: url('contact2.png');

    display: -ms-flexbox;

    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: start;
        justify-content: flex-start;
    overflow-y: scroll;
    overflow-x: hidden;
    text-align: center;
    -ms-flex-line-pack: center;
        align-content: center;

    background-position-y: 0;
    background-repeat: no-repeat;
    background-clip: border-box;
    background-size: auto 350px;
  }
  
  .scrollable {
    width: 100%;
    margin-top: 0;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    margin-left: 0;
    margin-right: 0;
    opacity: 1;
    position: static;
    top: 0;
    background-color: #ffffff;
  }

  .scrollable .chevron {
    display: none;
  }

  .scrollable .profile .summary {
    font-size: 0.95rem;
    margin-top: 40px;
  }

  .scrollable .skills p {
  }
}

