  }
  
}

@media screen and (max-width: 430px){

  .scrollable .profile .summary {
    margin-top: 20px;
  }

  .scrollable .skills p {
  }
  
}

@media screen and (max-width: 375px){

  .scrollable .profile .summary {
    margin-top: 20px;
  }

  .scrollable .skills p {
  }

  .scrollable h1 {
    font-size: 1.5rem;
  }
  
}

@media screen and (max-height: 800px){

  .scrollable {
    top: 80vh;
  }
  
}

@media screen and (max-height: 1100px){

  .scrollable {
    top: 94vh;
  }
  
}




